<template>
	<div class="download">
		<img src="../images/download.png">
		<div class="step01" @click="download">立即申请</div>
		<div class="step02" v-show="isShow">
			<img src="../images/yindao.png" @click="hide">
		</div>
	</div>
</template>
<script>
export default {
	data() {
		return {
			isShow: false
		}
	},
	methods: {
		download() {
			// var ua = window.navigator.userAgent.toLowerCase()
			var ua = window.navigator.userAgent
			if (ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1) {
				let u = window.navigator.userAgent.toLowerCase()
				if (u.indexOf('micromessenger') !== -1) {
					this.isShow = true
				} else {
					window.location = 'http://m.51jjhao.com/act/downloadApk.htm'
				}
			} else if (ua.indexOf('iPhone')) {
				window.location = 'https://itunes.apple.com/cn/app/id1324980243?mt=8'
			}
		},
		hide() {
			this.isShow = false
		}
	},
	mounted() {

	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.download {
		position: relative;
		width: 100%;
		height: 100%;
		img {
			width: 100%;
			height: 100%;
			vertical-align: bottom;
		}
		.step01 {
			position: absolute;
			bottom: 0.7rem;
			font-size: 0.32rem;
			letter-spacing: 0.03px;
			color: #6eabd1;
			line-height: 0.86rem;
			height: 0.86rem;
			text-align: center;
			width: 4.19rem;
			background: #fff;
			border-radius: 0.15rem;
			left: 50%;
			transform: translateX(-50%);
		}
		.step02 {
			position: absolute;
			left: 0;
			bottom: 0;
			top: 0;
			right: 0;
			z-index: 100;
			top: 0;
			img {
				width: 100%;
				vertical-align: bottom;
			}
		}
	}
</style>



